.container-loja-page {
    min-height: 40vw;
    width: 100%;
}

.line-detail {
    font-family: 'Roboto Condensed';
    height: 6vw;
    text-align: left;
}

.text-line-detail {
    font-weight: normal;
    font-size: 2.8vw;
    letter-spacing: -0.05vw;
    color: #fff;
    height: 100%;
    padding-top: 1.2vw;
    padding-left: 4vw;
}

.totalResults {
    color: #676767;
    margin-top: 4vw;
    text-align: left;
    margin-left: 6vw;
}

.products-grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin: 0 3vw;
    text-align: left;
    min-height: 80vw;
}

.produtos .title {
    width: 36%;
}

.text {
    align-self: center;
}

.pagination-container {
    width: 100%;
    padding-bottom: 2vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.pagination {
    display: flex;
    width: 100%;
    justify-content: center;
}

.pagination-item {
    padding: 1.2vw;
    font-weight: bold;
    font-size: 1.2vw;
    border: #676767 solid 1px;
    color: #63a82e;
}