.container-toolbar {
    width: 100%;
    background-color: #fff;
    
}

.line {
    background-color: #7AD03A;
    height: 1vw;
}

.Toolbar {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 6.5vw;
    position: sticky;
    position: -webkit-sticky;
    
}

.button-frame {
    height: 100%;
    align-self: center;
    display: flex;
    justify-content: space-between;
}

.button {
    background-color: #fff;
    text-decoration: none;
    border: none;
    color: #515151;
    padding: 0 1.4vw;
    font-size: 1.2vw;
    display:grid; 
    justify-content:center;
}

.text {
    align-self: center;
}

.button:hover {
    background-color: #7AD03A;
    color: #fff;
    transition: linear 0.2s all;
}

.button:active {
    background-color: #7AD03A;
    color: #fff;
    transition: linear 0.2s all;
}

#login {
    cursor: pointer;
}

.withBorder {
    background-color: #7AD03A;
    color: #fff;
}

.logo {
    align-self: center;
    margin-right: 20vw;
}
.logo-toolbar {height: 5vw;}

.logo {width: 24%; background-color: #fff;}
.logo, .button {height: 100%;}

.Toolbar .cart-icon {
    width: 2vw;
    height: 80%;
    padding-top: 20%;
}

.arrow-detail {
    height: auto;
    width: 2vw;
    margin-left: .5vw;
}

.overlay-base, .content-base {
    position: absolute;
    margin-top: 4.2vw;
    top: 0;
    right: 0;
    margin-right: 7vw;
    /* margin-left: 50vw; */
    left: 1;
    opacity: 1;
    transition-property: background-color, width, opacity;
    transition-duration: 100ms;
    transition-timing-function: ease-in-out;

    display: flex;
    flex-direction: column;
    border: rgb(176, 192, 180) solid .1px;
}

.overlay-after { opacity: 1; }
.overlay-before { opacity: 0; }

.content-base {
    
}

.content-after {
    width: 20.5vw;
    height: auto;
    background-color: #fff;
}

.button-close svg {
    height: 1.8vw;
    width: 1.8vw;
    color: #f1e8e8;
}

.button-close {
    background: none;
    border: none;
    font-weight: bold;
    padding: 0;
    align-items: top;
    height: 2vw;
    margin-left: 2vw;
    margin-top: 1vw;
    width: 100%;
}

.content-before { background-color: transparent; }

.name-info, .email-info, .email-info-title {
    font-size: 1.4vw;
    text-align: left;
    margin-top: 1vw;
    margin-left: 2.5vw;
}

.name-info {margin-top: 1.8vw;}
.name-info br {
    margin-top: -1vw;
}

.email-info-title {
    font-size: 1.2vw;
}

.email-info {
    margin-top: 0vw;
}

.first-box {
    display: flex;
    flex-direction: column;
    background-color: #63a82e;
    width: 100%;
    color: #f8f5f5;
    padding-bottom: 1vw;
    padding-top: 1vw;
}

.second-box {
    padding-top: 1vw;
    padding-bottom: 2vw;
}

.logout {
    padding-top: 1vw;
    font-size: 1.4vw;
    font-weight: 600;
    font-family: 'Roboto Condensed';
    letter-spacing: 0vw;
    width: 50%;
    margin-left: 48%;
    margin-top: 1vw;
    background: none;
    border: none;
    color: #fff;
}