@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap');

.Container {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    background: #FAFBFD;
}

.Container h1, .Container p { font-family: 'Poppins'; font-size: 1.3vw; align-self: center; margin-top: .6vw;}
.Container h1{ 
    color: #333; 
    font-weight: 600; 
    font-size: 3vw; 
    margin-top: 4vh;
    margin-bottom: -1vw;
}

button {
    cursor: pointer;
}

.Container .line-detail {
    height: 1vw;
}

.Center {
    width: 40%;
    margin-bottom: 2%;
    display: flex;
    flex-direction: column;
    align-self: center;
    min-height: 62vh;
    margin-bottom: 1vw;
}


.icon {
    margin-left: 2vw;
    width: 2.5vw;
    height: 100%;
    background-repeat: no-repeat;
    background-size:contain;
}

 .EmailFrame, .PasswordFrame {
    border: 1px solid #000000;
    box-sizing: border-box;
    border-radius: 10px;
    width: 100%;
    height: 3.8vw;
    background: none;
    display: flex;
    margin-top: .8vw;
}

.EmailFrame input, .PasswordFrame input {
    border: 0;
    font-size: 2vw;
    font-family: 'Poppins';
    background: none;
    margin-left: 2vw;
    width: 100%;
}

.LastPassword {
    width: 100%;
    font-family: 'Poppins';
    height: 4vw;
    margin-bottom: .4vw;
    float: right;
    background: none;
    border: none;
    color:#217377;
    font-size: 1.3vw;
    font-weight: 600;
    font-style: italic;
}

.EmailFrame input::placeholder, .PasswordFrame input::placeholder {
    color: #333333;
    font-weight: 500;
}

.EmailFrame input:focus, .PasswordFrame input:focus {
    outline: none;
}

.EmailFrame .icon {
    background-image: url('../../../public/images/login/Email_icon.svg');
    margin-top: 0.6vw;

}

.PasswordFrame .icon {
    background-image: url('../../../public/images/login/Password_icon.svg');
    margin-top: 0.8vw;
}

.ButtonFrame {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 4.4vw;
    margin-top: 0vw;
}

.ButtonFrame button {
    width: 100%;
    border-radius: 0.6em;
    border: 0.2vw solid #71e41a;
    color: #669443;
    font-size: 1.8vw;
    font-family: 'Poppins';
    font-weight: 600;
}

.GoogleOption button {
    display: flex;
    height: 4vw;
    margin-top: 1vw;
    width: 100%;
    border-radius: 0.6em;
    border: 0;
    color: #fff;
    font-size: 1.5vw;
    font-family: 'Poppins';
    font-weight: 600;
    background-color: #9bc978;
}
.GoogleOption p {
    margin-left: 2vw;
    font-size: 1.4vw;
}
.GoogleIcon {
    background-image: url('../../../public/images/login/GoogleIcon.svg');
    background-repeat: no-repeat;
    background-size:contain;
    width: 2vw;
    height: 2vw;
    align-self: center;
    margin-left: 8vw;
}

.CreateAccont {
    display: flex;
    width: 40vw;
    font-family: 'Poppins';
    height: 4vw;
    margin-bottom: .8vw;
    font-size: 1.4vw;
    font-weight: 600;
    font-style: italic;
}
.CreateAccont button {
    font-family: 'Poppins';
    background: none;
    border: none;
    font-style: italic;
    font-size: 1.4vw;
    font-weight: 600;
    color:#37C2CA;
    font-size: 1.5vw;
    margin-left: 4vw;
    transition: 0.2s;
}

.CreateAccont button:hover {

    color:#34878b;
    transition: 0.2s;
}